.six {
  min-height: 600px;
  max-height: 1000vh !important;
  overflow: hidden;
}

.img-7 {
  width: 480px;
  height: 400px;
  position: absolute;
  top: 60px;
  right: 30px;
  background-image: url("../../assets/images/Chidren/7.jpg");
  background-position: center center;
  background-size: 100% 100%;
  z-index: 0;
}

.img-7-shade {
  width: 480px;
  height: 400px;
  position: absolute;
  z-index: 1;
  top: 60px;
  right: 30px;
  background-color: #a356ff;
  background-position: center center;
  opacity: 0.25;
  background-size: 100% 100%;
}

.absolute-container-six {
  height: 700px !important;
}

.img-8 {
  width: 300px;
  height: 200px;
  position: absolute;
  top: 480px;
  left: 300px;
  background-image: url("../../assets/images/Chidren/8.jpg");
  background-position: center center;
  background-size: 100% 100%;
  z-index: 0;
}

.img-8-shade {
  width: 300px;
  height: 200px;
  position: absolute;
  z-index: 1;
  top: 480px;
  left: 300px;
  background-color: #a356ff;
  background-position: center center;
  opacity: 0.25;
  background-size: 100% 100%;
}

@media (max-width: 500px) {
  .absolute-container-six {
    height: 500px !important;
  }
  .img-7,
  .img-7-shade {
    width: 280px;
    height: 200px;
    right: 30px;
  }
  .img-8,
  .img-8-shade {
    top: 270px;
    left: 30px;
    width: 250px;
    height: 170px;
  }
}

@media (max-width: 991px) and (min-width: 500px) {
  .absolute-container-six {
    height: 800px !important;
  }
  .six {
    max-height: 1500px;
  }
}

@media (max-width: 991px) {
  .six {
    flex-direction: column-reverse;
  }
}

@media (max-width: 680px) and (min-width: 501px) {
  .img-8,
  .img-8-shade {
    left: 10%;
  }
}
